@import "./src/sass/helpers/var";

.chooseLanguage {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  // justify-self: flex-end
  // width: 7.5%
  // margin: 0 auto
  margin-right: 1.5rem;
  &__item {
    display: flex;
    font-size: calc(0.25rem + 0.75vw);
    line-height: calc(0.25rem + 1vw);
    border: none;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding: 0 0.5rem;
    transition: $transition;
    color: $blue;
    background-color: transparent;

    &:hover {
      cursor: pointer;
      color: $light-blue; }

    &:nth-child(2n) {
      border: {
        left: 1px solid $blue;
        right: 1px solid $blue; } } } }

@media screen and ( max-width: 1024px) {
  .chooseLanguage {
    &__item {
      font-size: calc(0.5rem + 1vw);
      line-height: calc(0.5rem + 1vw); } } }
