@import "../helpers/_var";

.stages {
    width: 100%;
    display: flex;
    flex-direction: column;

    &--wrapper {
        width: 75%;
        margin: 0 auto; }

    &__link {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: calc(0.5rem + 3vw) 0;
        line-height: calc(0.5rem + 1.25vw);
        font-size: calc(0.25rem + 0.75vw); } }

.stages-group {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: auto;
    height: auto;
    grid-gap: calc(0.5rem + 2vw) calc(0.5rem + 5vw);
    flex-wrap: wrap;
    align-content: center;
    justify-item: center;

    position: relative;
    &::after {
        display: block;
        position: absolute;
        content: url('../img/pattern-4.png');
        top: 0;
        left: 27.5%;
        transform: translate(-50%, 0%); }

    &__title {
        width: 100%;
        text-align: right;
        font-size: calc(0.25rem + 2.25vw);
        line-height: calc(0.5rem + 2.5vw);
        z-index: 5; }

    &__item {
        width: 100%;
        display: flex;
        flex-direction: column;
        z-index: 5; } }

.align-items--end {
    align-items: flex-end;
    text-align: right; }

.stages-description {
    &__icon {
        display: flex;
        justify-content: center;
        align-items: center;
        width: calc(1rem + 2.5vw);
        height: calc(1rem + 2.5vw);
        img {
            height: 100%;
            object-fit: contain; } }

    &__title {
        padding: calc(0.5rem + 1vw) 0;
        font-size: calc(0.25rem + 1vw);
        font-weight: 600;
        line-height: calc(0.5rem + 1.25vw); }

    &__legend {
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw); } }

@media screen and ( max-width: 1280px) {
    .stages {
        &--wrapper {
            width: 85%; } } }

@media screen and ( max-width: 768px) {
    .stages {
        &__link {
            font-size: calc(0.5rem + 1vw);
            line-height: calc(0.5rem + 1.25vw); } }

    .stages-group {
        &::after {
            display: none; }
        &__title {
            font-size: calc(0.25rem + 4vw);
            line-height: calc(0.5rem + 4vw); } }

    .stages-description {
        &__icon {
            width: calc(1.25rem + 5vw);
            height: calc(1.25rem + 5vw); }

        &__title {
            font-size: calc(0.5rem + 0.75vw);
            line-height: calc(0.5rem + 1.75vw); }

        &__legend {
            text-align: justify;
            font-size: calc(0.5rem + 0.65vw);
            line-height: calc(0.5rem + 1.75vw); } } }

@media screen and ( max-width: 675px) {
    .stages {
        &__link {
            font-size: calc(0.5rem + 1.25vw);
            line-height: calc(0.5rem + 2.5vw); } }

    .stages-group {
        justify-items: center;
        grid-template-columns: repeat(1, 1fr);
        &__title {
            font-size: calc(0.25rem + 5vw);
            line-height: calc(0.5rem + 5vw); } }

    .stages-description {
        width: 90%;
        align-items: center;
        &__icon {
            width: calc(2rem + 5vw);
            height: calc(2rem + 5vw); }

        &__title {
            text-align: center;
            font-size: calc(0.5rem + 1.5vw);
            line-height: calc(0.5rem + 2.75vw); }

        &__legend {
            text-align: center;
            font-size: calc(0.5rem + 1.25vw);
            line-height: calc(0.5rem + 2.5vw); } } }
