@import "../helpers/var";

.services {
    width: 100%;
    display: flex;
    background: url('../img/services__background.png') center / contain no-repeat;

    &--wrapper {
        display: flex;
        flex-direction: column;
        width: 75%;
        margin: 0 auto;
        padding: 0 0 5%; }

    &__title {
        font-size: calc(0.25rem + 2.5vw);
        line-height: calc(0.5rem + 2.5vw);
        padding: 5% 0;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        position: relative;
        &::before {
            position: absolute;
            top: 0;
            left: 0;
            transform: translate(0, -25%);
            width: 100%;
            height: 100%;
            display: block;
            content: "Стадії проектування";
            font-size: $fons-size-title--before;
            line-height: $line-height-title--before;
            color: $title-before; } }

    &__link {
        line-height: calc(0.5rem + 1.25vw);
        font-size: calc(0.25rem + 0.8vw); } }

.services-box-group {
    background-position: 10% center;
    background-size: cover;
    background-repeat: no-repeat;
    padding: 5% 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 5vw;

    &__item {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        text-align: center;
        padding: 7.5% 2.5%;
        transition: all 0.3s ease;
        text-transform: uppercase;
        position: relative;
        background-color: #def1fc;
        box-shadow: 15px 15px 25px 0 rgba(0,0,0,0.25);

        &--right {
            transform: translateY(75%); }

        &:hover {
            background-color: rgba(222, 241, 252, 0.5);
            cursor: default;
            transition: all 0.3s ease;
            box-shadow: 0 0 2px 5px rgba(0,0,0,0.1); } } }

.services-description {
    &__number {
        position: absolute;
        display: flex;
        top: 0;
        left: 0;
        color: $light-blue;
        font-size: calc(0.25rem + 5vw);
        line-height: calc(0.5rem + 5vw);
        font-weight: 700;
        transform: translate(25%, -50%); }

    &--wrapper {
        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 0 5%;
        justify-content: space-evenly;
        width: 100%;
        text-align: left; }

    &__arrow {
        position: absolute;
        bottom: 0;
        right: 0;
        transform: translate(-25%, 40%);
        display: flex;
        font-size: calc(0.25rem + 6vw);
        line-height: calc(0.5rem + 6vw);
        color: #4c66a6;
        font-weight: bold;
        height: calc(2.5rem + 5vw);
        width: calc(2.5rem + 5vw);
        transition: all 1s ease-in-out;
        animation: reverseTranslateRight 1s ease-in-out forwards;

        svg {
            width: 100%;
            height: 100%; } }

    &__icon {
        margin: 5% auto;
        height: calc(1.5rem + 5vw);
        width: calc(1.5rem + 10vw);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;

        &1 {
            background-image: url("../img/services-1.png"); }
        &2 {
            background-image: url("../img/services-2.png"); }
        &3 {
            background-image: url("../img/services-3.png"); }
        &4 {
            background-image: url("../img/services-4.png"); }
        &5 {
            background-image: url("../img/services-5.png"); } }

    &__title {
        font-weight: 600;
        line-height: calc(0.5rem + 1.5vw);
        font-size: calc(0.25rem + 1vw); }

    &__legend {
        text-align: justify;
        text-transform: none;
        margin: calc(0.5rem + 1.25vw) auto;
        line-height: calc(0.5rem + 1.25vw);
        font-size: calc(0.25rem + 0.8vw); }

    &__link {
        padding: calc(0.5rem + 0.5vw);
        color: $blue;
        transform: scale(0);
        text-decoration: none;
        width: 50%;
        text-align: center;
        font-size: calc(0.25rem + 0.6vw);
        line-height: calc(0.5rem + 0.75vw);
        border: 1px solid #6993fe; }

    &:hover {
        & .services-description__link {
            transform: scale(1);
            transition: all 0.3s ease; }

        & .services-description__arrow {
            transition: all 1s ease-in-out;
            animation: arrowTranslateRight 0.3s ease-in-out forwards; } }

    &:nth-child(2n) {
        & .services-description__arrow {
            animation: reverseTranslateLeft 0.3s ease-in-out forwards;
            left: 10%;
            svg {
                transform: rotateY(180deg);
                left: 0; } }
        &:hover {
            & .services-description__arrow {
                animation: arrowTranslateLeft 0.3s ease-in-out forwards; } } }

    &:last-child {
        & .services-description__arrow {
            display: none; } } }

.services__link {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    &-item {
        font-size: calc(0.25rem + 0.75vw);
        display: inline-block; } }

@keyframes arrowTranslateRight {
    from {
        transform: translate(-25%, 40%); }

    to {
        transform: translate(0, 40%) rotateZ(-90deg); } }

@keyframes arrowTranslateLeft {
    from {
        transform: translate(0, 40%); }

    to {
        transform: translate(-50%, 40%) rotateZ(90deg); } }

@keyframes reverseTranslateRight {
    from {
        transform: translate(0, 40%) rotateZ(-90deg); }

    to {
        transform: translate(-25%, 40%); } }

@keyframes reverseTranslateLeft {
    from {
        transform: translate(-50%, 40%) rotateZ(90deg); }

    to {
        transform: translate(0, 40%); } }

@media screen and ( max-width: 1366px) {
    .services {
        &__title {
            &::before {
                font-size: calc(1.25rem + 5vw);
                line-height: calc(1.5rem + 5vw); } } }

    .services-box-group {
        grid-gap: 7.5vw; } }

@media screen and ( max-width: 1280px) {
    .services {
        &--wrapper {
            width: 85%; } } }

@media screen and ( max-width: 768px) {
    .services {
        &__link {
            line-height: calc(0.5rem + 2.25vw);
            font-size: calc(0.25rem + 1.75vw); }
        &__title {
            font-size: calc(0.5rem + 4vw);
            line-height: calc(0.5rem + 4vw);

            &::before {
                font-size: calc(1rem + 6.5vw);
                line-height: calc(1.25rem + 6.5vw); } } }

    .services-box-group {
        grid-template-columns: repeat(1, 1fr);
        margin: 0 auto;
        width: 85%;
        grid-gap: 10vw 0;

        &__item {
            padding: 10% 5%;

            &--right {
                transform: translateY(0); } } }

    .services-description {
        &__number {
            font-size: calc(0.25rem + 8vw);
            line-height: calc(0.5rem + 8vw); }

        &__title {
            line-height: calc(0.5rem + 2.25vw);
            font-size: calc(0.25rem + 2vw); }

        &__legend,
        &__link {
            // margin: calc(0.5rem + 1.25vw) auto
            line-height: calc(0.5rem + 2.25vw);
            font-size: calc(0.25rem + 1.75vw);
            transform: scale(1); } } }

@media screen and ( max-width: 480px) {
    .services {
        &__title {
            &::before {
                font-size: calc(1rem + 5vw);
                line-height: calc(1.25rem + 5vw); } } }

    .services-box-group {
        width: 100%; } }
