@import "../helpers/var";

.location {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 3.5% 0 0;
    background: $blue url('../img/project__map.png') 90% center / 45% no-repeat;

    &--main {
        color: $blue;
        padding: 3.5% 0;
        background: $white url('../img/map.png') right center / 50% no-repeat;

        .location__link-item {
            color: $blue; }

        .footer-description__title {
            color: $blue;
            margin: calc(0.25rem + 3.5vw) 0 0;
            font-size: calc(0.25rem + 1vw);
            line-height: calc(0.5rem + 1.25vw); }

        .footer-address__item {
            color: $blue; }

        .contacts-link {
            &__caption,
            &__item {
                color: $blue; } } }

    &--wrapper {
        width: 75%;
        display: flex;
        flex-direction: column;
        margin: 0 auto;
 }        // margin: 0 auto 0 15%

    &--margin {
        & .location--wrapper {
            margin: 0 auto 0 12.5%; }

        & .footer-description {
            width: 70%; }

        & .contacts-form {
            z-index: 10;
            width: 50vw; } }

    &__title {
        font-size: $fons-size-title;
        line-height: $line-height-title;
        width: 100%;
        display: flex;
        text-align: left;
        position: relative;
        &::before {
            position: absolute;
            top: 0;
            left: 0;
            transform: translate(-15%, 0%);
            width: 100%;
            height: 100%;
            display: block;
            content: "Локація";
            font-size: $fons-size-title--before;
            // line-height: calc(0.75rem + 2.75vw)
            color: $title-before; } }

    &__link {
        margin: 1rem 0;
        &-item {
            text-decoration: underline;
            line-height: calc(0.5rem + 1.25vw);
            font-size: calc(0.25rem + 0.75vw);
            padding: calc(0.5rem + 0.5vw) 0;
            color: $white;

            &:hover {
                color: $light-blue; } } } }

.footer-description {
    display: flex;
    flex-direction: column;
    width: 40%;

    &__title {
        display: flex;
        width: 100%;
        font-size: calc(0.25rem + 1vw);
        line-height: calc(0.5rem + 1.5vw);
        font-weight: 600;
        text-transform: uppercase;
        color: $white; } }

.footer-address {
    display: flex;
    flex-direction: column;
    // width: 100%
    margin: calc(0.5rem + 1.75vw) 0;

    &__item {
        display: inline-block;
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw);
        color: $white; } }

.footer-contacts {
    display: flex;
    flex-direction: column;
    // width: 100%

    &__item {
        display: flex;
        width: inherit;
        flex-direction: column; } }

.contacts-link {
    &__caption {
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw);
        color: $white; }

    &__item {
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw);
        color: $white; } }

@media screen and ( max-width: 1280px) {
    .location {
        &--wrapper {
            width: 85%; }
        &--margin {
            & .location--wrapper {
                width: 50%;
                & .footer-description {
                    width: 100%; } }

            & .contacts-form {
                width: 50vw; } }

        &__title {
            &::before {
                transform: translate(-7.5%, 0); } } } }

@media screen and ( max-width: 768px) {
    .location {
        &__title {
            font-size: calc(0.25rem + 4vw);
            line-height: calc(0.5rem + 4vw);
            &::before {
                font-size: calc(2.5rem + 3.5vw); } }

        &--main {
            .footer-description__title {
                font-size: calc(0.5rem + 1.25vw);
                line-height: calc(0.75rem + 1.5vw); } }

        &--margin {
            & .footer-description {
                &__title {
                    font-size: calc(.5rem + 0.5vw); } }

            & .footer-address {
                &__item {
                    font-size: calc(0.5rem + 0.75vw);
                    line-height: calc(0.75rem + 1vw); } }

            & .contacts-link {
                &__caption {
                    font-size: calc(0.5rem + 0.75vw);
                    line-height: calc(0.5rem + 1.75vw); }

                &__item {
                    font-size: calc(0.5rem + 0.75vw);
                    line-height: calc(0.5rem + 1.75vw); } }

            & .contacts-form {
                width: 60vw;

                &__title {
                    font-size: calc(0.5rem + 1.5vw);
                    line-height: calc(0.5rem + 1.5vw); }
                &__input,
                &__textarea,
                &__btn {
                    line-height: calc(0.5rem + 1vw);
                    font-size: calc(0.5rem + 1vw);
                    padding: 0.75rem; } } }

        &__link {
            &-item {
                line-height: calc(0.5rem + 1.5vw);
                font-size: calc(0.5rem + 1vw); } } }

    .footer-description {
        width: 100%;
        &__title {
            text-align: center;
            font-size: calc(0.5rem + 2vw);
            line-height: calc(0.5rem + 2vw); } }

    .footer-address {
        &__item {
            font-size: calc(0.5rem + 1vw);
            line-height: calc(0.5rem + 1.5vw); } }

    .contacts-link {
        &__caption {
            font-size: calc(0.5rem + 1vw);
            line-height: calc(0.5rem + 2vw); }

        &__item {
            font-size: calc(0.5rem + 1vw);
            line-height: calc(0.5rem + 2vw); } } }

@media screen and ( max-width: 700px) {
    .location {
        background-image: none;
        &--wrapper {
            align-items: center; }

        &--margin {
            flex-direction: column-reverse;

            & .location--wrapper {
                width: 80%;
                margin: 0 auto; }

            & .contacts-form {
                width: 60%;
                margin: 2.5vw auto 5vw;
                // margin: -7.5vw auto 5vw
                padding: 7.5% 5%;
                &__title {
                    font-size: calc(.5rem + 1.75vw); } }

            & .footer-description {
                &__title {
                    font-size: calc(.5rem + 2.25vw);
                    line-height: calc(.5rem + 2.75vw); } }

            & .footer-address {
                &__item {
                    font-size: calc(0.5rem + 1.75vw);
                    line-height: calc(0.75rem + 1.75vw); } }

            & .contacts-link {
                &__caption {
                    font-size: calc(0.5rem + 1.75vw);
                    line-height: calc(0.75rem + 2.5vw); }

                &__item {
                    font-size: calc(0.5rem + 1.75vw);
                    line-height: calc(0.75rem + 2.5vw); } } } }

    .footer-description {
        display: grid;
        grid-template-columns: repeat(2, 1fr);

        flex-direction: row;
        width: 100%;

        &__title {
            text-align: center;
            justify-content: center;
            align-items: center;
            grid-column: 1/3; } }

    .footer-address {
        grid-column: 2/3;
        grid-row: 2/3;
        align-items: flex-end; }
    .footer-contacts {
        grid-column: 1/2;
        grid-row: 2/3;
        margin: calc(0.5rem + 1.75vw) 0; } }

@media screen and ( max-width: 480px) {
    .location {
        &__title {
            font-size: calc(0.25rem + 6vw);
            line-height: calc(0.5rem + 6vw); }

        &--main {
            .footer-description__title {
                width: 100%;
                font-size: calc(0.5rem + 1.75vw);
                line-height: calc(0.75rem + 3vw); } }

        &__link {
            &-item {
                line-height: calc(0.5rem + 3vw);
                font-size: calc(0.5rem + 2vw); } }

        &--margin {
            flex-direction: column-reverse;

            & .contacts-form {
                width: 80vw;
                height: auto;
                // margin: -15vw auto 5vw
                padding: 7.5% 5%;
                &__title {
                    font-size: calc(.5rem + 1.75vw); } } } }

    .footer-description {
        &__title {
            font-size: calc(0.5rem + 1.75vw);
            line-height: calc(0.75rem + 2vw); } }

    .footer-address {
        &__item {
            font-size: calc(0.5rem + 2vw);
            line-height: calc(0.5rem + 2.25vw); } }

    .contacts-link {
        &__caption {
            font-size: calc(0.5rem + 2vw);
            line-height: calc(0.5rem + 3vw); }

        &__item {
            font-size: calc(0.5rem + 2vw);
            line-height: calc(0.5rem + 3vw); } } }

@media screen and (max-device-width: 767px) and (min-device-width: 320px) and (orientation: landscape) {
    .location {
        &--margin {
            & .contacts-form {
                display: flex;
                grid-gap: 0;
                padding: 2.5rem;

                &__input,
                &__textarea,
                &__btn {
                    margin: auto; } } } } }
