@import "./src/sass/helpers/var";

.footer {
  width: 100%;
  display: flex;
  align-items: center;
  background-color: rgba($blue, 1);
  justify-content: center; }

.copyright {
  font-size: calc(0.35rem + 1vw);
  line-height: calc(0.5rem + 1.25vw);
  margin: 2.5% 0;
  padding: 0;
  color: $white; }

@media screen and ( max-width: 768px) {
  .copyright {
    font-size: calc(0.5rem + 1.25vw);
    line-height: calc(0.5rem + 1.5vw); } }

