@import "../helpers/var";

.contacts-form {
    display: flex;
    width: 35vw;
    flex-direction: column;
    margin: -7.5vw 12.5vw 0 auto;
    padding: 2.5%;
    background-color: $white;
    justify-content: space-between;
    align-items: center;

    &__title {
        display: flex;
        font-weight: 500;
        text-align: center;
        width: 75%;
        font-size: calc(0.25rem + 1.2vw);
        line-height: calc(0.5rem + 1.5vw);
        padding-bottom: 1.5rem; }

    &__input {
        width: 100%;
        margin: calc(0.5rem + 0.25vw) 0;
        padding: calc(0.5rem + 0.5vw);
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw); }

    &__textarea {
        width: 100%;
        margin: calc(0.5rem + 0.25vw) 0;
        padding: calc(0.5rem + 0.5vw);
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw); }

    &__btn {
        width: 100%;
        display: flex;
        margin: calc(0.5rem + 0.5vw) 0;
        padding: calc(0.5rem + 0.5vw);
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw);
        justify-content: center;
        align-items: center;
        text-align: center;
        border: none;
        color: $white; } }

.submit {
    background-color: $light-blue; }
.close {
    background-color: $red; }

@media screen and ( max-width: 1024px) {
    .contacts-form {
        width: 45vw; } }

@media screen and ( max-width: 768px) {
    .contacts-form {
        width: 70vw;
        padding: 5%;

        &__title {
            width: 100%;
            font-size: calc(0.5rem + 2.5vw);
            line-height: calc(0.5rem + 2.5vw); }
        &__input,
        &__textarea,
        &__btn {
            line-height: calc(0.5rem + 1.5vw);
            font-size: calc(0.5rem + 1.5vw);
            padding: 1.5rem; } } }

@media screen and ( max-width: 480px) {
    .contacts-form {
        width: 100vw;
        height: 100vh;
        justify-content: center;

        &__title {
            line-height: calc(0.5rem + 3.5vw);
            font-size: calc(0.5rem + 3.5vw); }
        &__input,
        &__textarea,
        &__btn {
            padding: 0.75rem 1.5rem;
            line-height: calc(0.5rem + 1.5vw);
            font-size: calc(0.5rem + 1.5vw); } } }

@media screen and (max-device-width: 767px) and (min-device-width: 320px) and (orientation: landscape) {
    .contacts-form {
        width: 100vw;
        height: 100vh;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 1.5rem;
        &__title {
            padding: 0;
            grid-column: 1/3; }
        &__input,
        &__textarea,
        &__btn {
            margin: 0;
            padding: 0.5rem; } }

    .input-comment {
        grid-column: 1/3; } }
