@import "./src/sass/helpers/var";

.logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 25%;
  margin: 1rem auto 1rem 0;

  &__link {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    margin: 0 auto;
    overflow: hidden;

    & img {
      height: 100%;
      object-fit: cover; } }

  &__caption {
    display: flex;
    width: 50%;
    text-transform: uppercase;
    font-size: calc(0.35rem + 0.65vw);
    line-height: calc(0.5rem + 1vw);
    font-weight: 500;
    text-align: left; } }

@media screen and ( max-width: 1024px) {
  .logo {
    margin: 0.5rem auto 0.5rem 0;
    width: 35%;
    &__caption {
      font-size: calc(0.5rem + 0.75vw);
      line-height: calc(0.5rem + 1.25vw); } } }

@media screen and ( max-width: 700px) {
  .logo {
    width: 37.5%; } }

@media screen and ( max-width: 480px) {
  .logo {
    width: 50%; } }

