@import "../helpers/var";

.blog {
    display: flex;
    width: 100%;
    padding: 10% 0 0;

    &--wrapper {
        display: grid;
        grid-template-columns: auto minmax(40vw, 35vw);
        width: 75%;
        margin: 0 auto;
        align-items: center;
        justify-items: flex-end; }

    &__item {
        display: flex;
        width: 100%; } }

.blog-description {
    flex-direction: column;
    justify-content: space-evenly;
    padding: 0 10% 15% 0;
    &__title {
        font-size: $fons-size-title;
        width: 100%;
        display: inline-block;
        // padding: 0 0 2.5%
        line-height: $line-height-title;
        position: relative;
        &::before {
            position: absolute;
            top: 0;
            left: 0;
            transform: translate(-40%, 0%);
            width: 100%;
            height: 100%;
            display: block;
            content: "Блог";
            font-size: $fons-size-title--before;
            // line-height: calc(0.75rem + 2.75vw)
            color: $title-before; } }

    &__caption {
        display: inline-block;
        font-size: calc(0.25rem + 1.25vw);
        width: 100%;
        padding: calc(0.5rem + 0.5vw) 0;
        font-weight: 600;
        line-height: calc(0.5rem + 1.5vw); }

    &__legend {
        margin: calc(0.5rem + 0.5vw) 0;
        display: inline-block;
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw);
        text-align: justify;
        width: 100%; }

    &__link {
        display: inline-block;
        margin: calc(0.5rem + 0.5vw) 0;
        padding: calc(0.5rem + 0.5vw);
        border: 1px solid $light-blue;
        width: 35%;
        text-align: center;
        line-height: calc(0.5rem + 0.75vw);
        font-size: calc(0.25rem + 0.6vw); } }

.blog-img {
    text-align: right;
    justify-content: center;
    width: 75%;

    &__link {
        display: flex;
        width: 100%;
        height: 100%;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover; } } }
@media screen and ( max-width: 1366px) {
    .blog-description {
        &__title {
            font-size: calc(.5rem + 3.25vw); } } }

@media screen and ( max-width: 1280px) {
    .blog {
        &--wrapper {
            width: 85%; } }
    .blog-description {
        &__title {
            &::before {
                transform: translate(-20%, 0%); } } } }

@media screen and ( max-width: 768px) {
    .blog {
        padding: 10% 0 0; }
    .blog-description {
        padding: 0;

        &__title {
            margin: 5% 0;
            font-size: calc(0.25rem + 4vw);
            line-height: calc(0.5rem + 4vw);
            &::before {
                font-size: calc(2.5rem + 3.5vw);
                transform: translate(-10%, 0%); } }

        &__caption {
            font-size: calc(.25rem + 2vw); }

        &__legend {
            font-size: calc(.25rem + 1.5vw);
            line-height: calc(.5rem + 1.5vw); }
        &__link {
            width: 100%;
            font-size: calc(.25rem + 1.5vw); } }
    .blog-img {
        width: 80%; } }

@media screen and ( max-width: 700px) {
    .blog {
        padding: 15% 0 0;
        &--wrapper {
            grid-template-columns: repeat(1, 1fr); } }
    .blog-description {
        padding-right: 0;
        &__title {
            font-size: calc(.25rem + 5vw); }

        &__caption {
            font-size: calc(.25rem + 3vw); }

        &__legend {
            font-size: calc(.25rem + 1.5vw);
            line-height: calc(.5rem + 2vw); }
        &__link {
            font-size: calc(.25rem + 1.5vw);
            width: 35%; } }
    .blog-img {
        width: 60%;
        margin: 2.5% auto;
        justify-self: center; } }

@media screen and ( max-width: 480px) {
    .blog {
        padding: 20% 0 0 0; }
    .blog-description {
        &__title {
            font-size: calc(0.25rem + 6vw);
            line-height: calc(0.5rem + 6vw); }

        &__caption {
            font-size: calc(.5rem + 3.25vw);
            line-height: calc(.5rem + 4vw); }

        &__legend {
            font-size: calc(.5rem + 2vw);
            line-height: calc(.5rem + 3.5vw); }
        &__link {
            width: 100%;
            padding: 1rem;
            font-size: calc(.25rem + 2.5vw); } }

    .blog-img {
        width: 100%; } }
