@import "./helpers/var";
@import "./helpers/animations";

.modal-form {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.35);
  transition: all 0.3s ease-in-out;
  z-index: 9999;

  &-contacts {
    margin: auto; } }

