@import "./src/sass/helpers/var";

.contacts {
  padding: 7.5% 0 0;
  width: 100%;

  &--wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 0 auto; }

  &__title {
    display: flex;
    padding: calc(2% + 1.5vw) 12.5%;
    font-size: $fons-size-title;
    line-height: $line-height-title;
    position: relative;
    &::before {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(0%, 35%);
      width: 100%;
      height: 100%;
      display: block;
      content: "Контакти";
      font-size: $fons-size-title--before;
      // line-height: calc(0.75rem + 2.75vw)
      color: $title-before; } } }

.map {
  &--wrapper {
    width: 100%;
    display: flex;
    z-index: 3;

    iframe {
      width: 100%; } } }

@media screen and ( max-width: 1366px) {
  .contacts {
    padding: 10% 0 0; } }

@media screen and ( max-width: 1280px) {
  .contacts {
    // padding: 12.5% 0 0
    // &__title
 } }    //   padding: calc(2% + 2.5vw) 7.5%

@media screen and ( max-width: 1024px) {
  .contacts {
    &__title {
      font-size: calc(0.5rem + 3vw);
      line-height: calc(0.5rem + 3vw); } } }

@media screen and ( max-width: 768px) {
  .contacts {
    padding: 12.5% 0 0;

    &__title {
      font-size: calc(0.25rem + 4vw);
      line-height: calc(0.5rem + 4vw);
      &::before {
        font-size: calc(3.5rem + 2.5vw); } } } }

@media screen and ( max-width: 480px) {
  .contacts {
    padding: 22.5% 0 0;
    &__title {
      font-size: calc(0.25rem + 6vw);
      line-height: calc(0.5rem + 6vw);

      &::before {
        font-size: calc(2.5rem + 2vw);
        transform: translate(0, 25%); } } } }

