*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  overflow-x: hiddenbody;
  color: #083147;
  font-family: "Montserrat";
  font-size: calc(0.25rem + 0.75vw);
}

section {
  width: 100%;
}

h1, h2, h3, h4 {
  color: #083147;
  transition: all 0.3s ease-in-out;
}

a {
  text-decoration: none;
  color: #083147;
  transition: all 0.3s ease-in-out;
}

a:hover {
  color: #6ab6f4;
}

a:hover h3 {
  color: #6ab6f4;
}

ul {
  list-style: none;
}

textarea {
  resize: none;
}

.link {
  align-items: center;
}

.link:hover .link__arrow {
  transform: translateX(100%) scaleX(2);
}

.container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 85%;
  height: 100%;
}

.main-content {
  width: 100%;
}

.title {
  text-transform: uppercase;
  font-weight: 700;
  color: #083147;
}

.bold {
  font-weight: 600;
}

.breadcrumbs {
  display: flex;
  margin: 2.5% 0;
  font-size: calc(0.25rem + 1vw);
}

.breadcrumbs__link .bold {
  color: #D7E790;
}

.transition {
  display: flex;
  position: fixed;
  z-index: 9999;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  margin: 0;
  pointer-events: none;
}

.transition li {
  transform: scaleY(0);
  background-color: #e65c00;
  transform-origin: bottom left;
  height: 100%;
  width: 20%;
}

.scroll--hidden {
  overflow: hidden;
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  pointer-events: none;
  z-index: 9999;
  visibility: hidden;
  opacity: 0;
  background-color: orange;
}

.scroll-up {
  position: absolute;
  top: 0;
  right: 7.5%;
  transform: translateY(-100%);
  background-color: #434344;
  width: 3.5vw;
  height: 2.5vw;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.scroll-up__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 50%;
  background-image: url("../img/sprite.svg#up-arrow");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

@media screen and (max-width: 768px) {
  .title {
    font-size: calc(0.25rem + 2.25vw);
    line-height: calc(0.5rem + 2.5vw);
  }
}

.header {
  width: 75%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background-color: white;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  padding: 0 5%;
  top: 5%;
  transition: all 0.3s ease;
}

.header--fixed {
  top: 0;
  width: 100%;
}

.header--adaptive {
  width: 100%;
  top: 0%;
  transition: all 0.3s ease-in-out;
}

.header--fullpage {
  flex-direction: column;
  width: 100%;
  height: 100vh;
  justify-content: center;
}

.header--fullpage .logo,
.header--fullpage .chooseLanguage {
  margin: 0 auto;
}

.header--fullpage .logo {
  width: 50%;
}

.header--fullpage .logo__caption {
  font-size: calc(0.5rem + 1vw);
  line-height: calc(0.5rem + 1.5vw);
}

.header--fullpage .chooseLanguage {
  display: none;
}

.header--fullpage .chooseLanguage__item {
  font-size: calc(0.5rem + 1vw);
}

@media screen and (max-width: 1366px) {
  .header {
    width: 85%;
  }
  .header--fixed {
    width: 100%;
  }
}

@media screen and (max-width: 1024px) {
  .header {
    width: 100%;
    top: 0;
  }
  .header--fullpage .logo {
    width: 37.5%;
  }
}

@media screen and (max-width: 768px) {
  .header--fullpage .logo {
    width: 45%;
  }
  .header--fullpage .logo__caption {
    font-size: calc(0.5rem + 1.5vw);
    line-height: calc(0.5rem + 2vw);
  }
}

@media screen and (max-width: 480px) {
  .header--fullpage .logo {
    width: 75%;
  }
}

.navigation {
  width: 60%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navigation--wrapper {
  display: inherit;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.navigation__item {
  display: flex;
  width: 100%;
}

.navigation__link {
  display: flex;
  padding: 3rem 0.75rem;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-size: calc(0.25rem + 0.75vw);
  transition: all 0.3s ease;
  color: #083147;
  font-weight: 500;
}

.navigation__link:hover, .navigation__link:focus, .navigation__link:active {
  color: #6ab6f4;
}

.menu-toggle {
  display: none;
  position: relative;
  width: 2rem;
  height: 2rem;
  margin: auto;
}

.menu-toggle:hover {
  cursor: pointer;
}

.menu-toggle span {
  margin: 0 auto;
  top: 12px;
  position: relative;
  width: 100%;
  height: 6px;
  background-color: #083147;
  display: block;
}

.menu-toggle span:before, .menu-toggle span:after {
  position: absolute;
  content: '';
  width: 100%;
  height: 6px;
  background-color: #083147;
  display: block;
}

.menu-toggle span:before {
  margin-top: -12px;
}

.menu-toggle span:after {
  margin-top: 12px;
}

.menu-toggle--active {
  position: absolute;
  top: 2.5%;
  right: 2.5%;
  transform: translate(-50%, 50%);
}

.menu-toggle--active span {
  transition-duration: 0s;
  background-color: rgba(8, 49, 71, 0);
  transition-delay: 0.2s;
}

.menu-toggle--active span:before, .menu-toggle--active span:after {
  transition-property: margin, transform;
  transition-duration: 0.2s;
  transition-delay: 0.2s, 0s;
  margin-top: 0;
}

.menu-toggle--active span:before {
  transform: rotate(45deg);
}

.menu-toggle--active span:after {
  transform: rotate(-45deg);
}

@media screen and (max-width: 1024px) {
  .menu-toggle {
    display: flex;
    margin: 0;
  }
  .navigation {
    display: none;
  }
  .navigation--active {
    display: flex;
    padding: 7.5% 0;
  }
  .navigation--active .navigation--wrapper {
    flex-direction: column;
  }
  .navigation__link {
    padding: 1.75rem 0;
    font-size: calc(0.5rem + 2vw);
  }
}

@media screen and (max-device-width: 767px) and (min-device-width: 320px) and (orientation: landscape) {
  .navigation--active {
    padding: 2.5% 0;
  }
  .navigation__link {
    padding: 0.5rem 0;
    font-size: calc(0.5rem + 2vw);
  }
}

