.hero {
  width: 100%;
  display: flex;
}

.hero--wrapper {
  display: flex;
  width: 75%;
  margin: 0 auto;
  padding: 7.5% 0;
  justify-content: center;
  align-items: flex-start;
  position: relative;
}

.hero--wrapper::after {
  display: block;
  position: absolute;
  content: url("../img/pattern-1.png");
  top: 0;
  right: 0;
  transform: translate(35%, 0%);
}

.about {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0 10% 0 0;
}

.about__title, .about__description {
  line-height: calc(0.5rem + 1.25vw);
  text-align: justify;
}

.about__title {
  font-size: calc(0.25rem + 1vw);
  font-weight: 600;
}

.about__description {
  font-size: calc(0.25rem + 0.75vw);
  padding: calc(0.25rem + 0.5vw) 0;
}

.about__link {
  display: flex;
  width: 50%;
  margin-top: calc(0.5rem + 0.5vw);
  font-size: calc(0.25rem + 0.75vw);
  line-height: calc(0.5rem + 1.25vw);
}

.hero-link {
  display: flex;
  width: 60%;
  justify-content: center;
  align-items: center;
  z-index: 5;
}

.hero-link:hover .hero-link__img img {
  transform: scale(0.975);
}

.hero-link__img {
  display: flex;
  position: relative;
  width: 100%;
  overflow: hidden;
}

.hero-link__img img {
  transition: all 0.3s ease-in-out;
  width: 100%;
  height: 100%;
