}

.hero-link--wrapper {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero-link__item {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  transition: background-size 0.3s ease-in-out;
  position: relative;
}

.hero-link:hover .hero-link__item {
  background-size: 105% 105%;
}

.hero-link:hover .hero-link__icon {
  color: #6ab6f4;
}

.hero-link__caption {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  line-height: calc(0.5rem + 2.25vw);
  font-size: calc(0.25rem + 1.75vw);
  font-weight: 500;
  padding: calc(0.5rem + 1.25vw);
  width: 75%;
