  left: 0;
}

.hero-link__icon {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 25%;
  height: 25%;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: calc(0.5rem + 2vw);
  color: #083147;
  transition: color 0.3s ease-in-out;
}

@media screen and (max-width: 1280px) {
  .hero--wrapper {
    width: 85%;
  }
}

