  padding: calc(0.5rem + 0.5vw);
  color: #083147;
  transform: scale(0);
  text-decoration: none;
  width: 50%;
  text-align: center;
  font-size: calc(0.25rem + 0.6vw);
  line-height: calc(0.5rem + 0.75vw);
  border: 1px solid #6993fe;
}

.services-description:hover .services-description__link {
  transform: scale(1);
  transition: all 0.3s ease;
}

.services-description:hover .services-description__arrow {
  transition: all 1s ease-in-out;
  animation: arrowTranslateRight 0.3s ease-in-out forwards;
}

.services-description:nth-child(2n) .services-description__arrow {
  animation: reverseTranslateLeft 0.3s ease-in-out forwards;
  left: 10%;
}

.services-description:nth-child(2n) .services-description__arrow svg {
  transform: rotateY(180deg);
  left: 0;
}

.services-description:nth-child(2n):hover .services-description__arrow {
  animation: arrowTranslateLeft 0.3s ease-in-out forwards;
}

.services-description:last-child .services-description__arrow {
  display: none;
}

