@import "../helpers/var";

.main-projects {
    display: flex;
    width: 100%;

    &--padding {
        padding: 7.5% 0 0; }

    &--wrapper {
        display: flex;
        width: 75%;
        flex-direction: column;
        margin: 0 auto;
        padding: 5% 0 0; }

    &__title {
        font-size: $fons-size-title;
        line-height: $line-height-title;
        width: 100%;
        position: relative;
        &::before {
            position: absolute;
            top: 0;
            left: 0;
            transform: translate(-15%, 0%);
            width: 100%;
            height: 100%;
            display: block;
            content: "Проекти";
            font-size: $fons-size-title--before;
            // line-height: calc(0.75rem + 2.75vw)
            color: $title-before; } } }

.projects-box {
    display: flex;
    width: 100%;
    flex-direction: column;
    padding: 5% 0;

    &__item {
        display: flex;
        width: 100%;
        // justify-content: center
        // padding: 2.5% 0

        &--left {
            flex-direction: row; }
        &--center {
            justify-content: center;
            & .project-description {
                margin: 0 5%;
                margin-bottom: calc(0.5rem + 5vw);
                justify-content: flex-end; } }

        &--right {
            flex-direction: row-reverse;
            & .project-description {
                align-items: flex-end;
                justify-content: center;
                text-align: right; } }

        &:hover {
            // & .project-hero__img
            & img {
                box-shadow: 2.5px 2.5px 12.5px 0 $blue;
                transform: scale(1.05); } } } }
.project {
    &__item {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        width: 40%; } }

.project-hero {
    &__img {
        // flex-basis: 25vh
        display: flex;
        transition: $transition;

        background: $light-blue;
        img {
            box-shadow: 5px 5px 25px 0 $blue;
            width: 100%;
            height: 100%;
            object-fit: contain;
            transition: all 0.3s ease-in-out; } }

    &__caption {
        position: absolute;
        top: 5%;
        left: 5%;
        color: $blue;
        padding: calc(0.5rem + 0.5vw) calc(0.5rem + 1vw);
        background-color: rgba($white, 0.35);
        border-radius: 5px;
        width: 50%;
        line-height: calc(0.5rem + 2vw);
        font-size: calc(0.25rem + 1.25vw); }

    &__link {
        display: flex;
        padding: calc(0.5rem + 1vw) calc(0.5rem + 0.5vw) calc(0.5rem + 2vw) 0;
        margin: calc(0.5rem + 0.75vw) 0;
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw); } }

.project-description {
    display: flex;
    flex-direction: column;
    width: 40%;
    margin: 0 auto;

    &__link {
        text-align: left;
        display: flex;
        font-size: calc(0.25rem + 2.25vw);
        font-weight: 600;
        line-height: calc(0.5rem + 2.25vw); }

    &__legend {
        padding: calc(0.5rem + 2vw) 0 calc(0.5rem + 2.5vw);
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw); } }

@media screen and ( max-width: 1280px) {
    .main-projects {
        &--wrapper {
            width: 85%; }

        &__title {
            &::before {
                transform: translate(-7.5%, 0); } } } }

@media screen and ( max-width: 1024px) {
    .main-projects {
        &__title {
            font-size: calc(0.5rem + 3.5vw);
            line-height: calc(0.5rem + 3.5vw); } } }

@media screen and ( max-width: 768px) {
    .main-projects {
        &__title {
            font-size: calc(0.25rem + 4vw);
            line-height: calc(0.5rem + 4vw);
            &::before {
                font-size: calc(2.5rem + 2.5vw); } } }

    .project {
        &-description {
            width: 50%;
            &__link {
                font-size: calc(1.5rem + 0.5vw); }

            &__legend {
                font-size: calc(0.5rem + 0.75vw);
                line-height: calc(0.5rem + 1.25vw); } }

        &-hero {
            &__caption {
                line-height: calc(0.5rem + 2vw);
                font-size: calc(0.25rem + 1.75vw);
                width: 75%; }

            &__link {
                line-height: calc(0.5rem + 0.75vw);
                font-size: calc(0.25rem + 1.25vw); } } } }

@media screen and ( max-width: 700px) {
    .projects-box {
        &__item {
            align-items: center;
            flex-direction: column;

            &--left,
            &--center,
            &--right {
                margin: calc(1rem + 1vw) 0;

                & .project-description {
                    align-items: center;
                    justify-content: center;
                    text-align: center; } } } }

    .project {
        &__item {
            width: 60%; }

        &-description {
            width: 80%;
            &__link {
                text-align: center;
                font-size: calc(1.5rem + 0.5vw);
                line-height: calc(1.5rem + 0.75vw); }

            &__legend {

                align-items: center;
                justify-content: center;
                font-size: calc(0.5rem + 0.75vw);
                line-height: calc(0.5rem + 1.25vw);
                text-align: center; } }

        &-hero {
            &__caption {
                line-height: calc(0.5rem + 2vw);
                font-size: calc(0.25rem + 1.75vw);
                width: 50%; }

            &__link {
                line-height: calc(0.5rem + 0.75vw);
                font-size: calc(0.25rem + 1.25vw); } } } }

@media screen and ( max-width: 480px) {

    .main-projects {
        &--padding {
            padding: 20% 0 0; }
        &__title {
            font-size: calc(0.25rem + 6vw);
            line-height: calc(0.5rem + 6vw); } }

    .projects-box {
        padding: 0; }

    .project {
        &__item {
            width: 80%; }

        &-description {
            width: 90%;
            &__link {
                font-size: calc(1rem + 1vw);
                line-height: calc(1rem + 1.5vw); }

            &__legend {
                font-size: calc(0.5rem + 1.25vw);
                line-height: calc(0.5rem + 1.75vw); } }

        &-hero {
            &__caption {
                line-height: calc(0.5rem + 4vw);
                font-size: calc(0.25rem + 3.5vw);
                width: 75%; }
            &__link {
                line-height: calc(0.5rem + 2.75vw);
                font-size: calc(0.25rem + 2.25vw); } } } }
